@tailwind base;
@tailwind components;
@tailwind utilities;

.gsi-material-button {
  user-select: none;
  background-color: white;
  border: 1px solid #747775;
  border-radius: 20px;
  box-sizing: border-box;
  color: #1f1f1f;
  cursor: pointer;
  font-family: 'Roboto', arial, sans-serif;
  font-size: 14px;
  height: 40px;
  letter-spacing: 0.25px;
  outline: none;
  overflow: hidden;
  padding: 0 12px;
  position: relative;
  text-align: center;
  transition: background-color 0.218s, border-color 0.218s, box-shadow 0.218s;
  vertical-align: middle;
  white-space: nowrap;
  max-width: 400px;
  min-width: min-content;
}

.gsi-material-button .gsi-material-button-icon {
  height: 20px;
  margin-right: 12px;
  min-width: 20px;
  width: 20px;
}

.gsi-material-button .gsi-material-button-content-wrapper {
  align-items: center;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  height: 100%;
  justify-content: space-between;
  position: relative;
  width: 100%;
}

.gsi-material-button .gsi-material-button-contents {
  flex-grow: 1;
  font-weight: 500;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: top;
}

.gsi-material-button .gsi-material-button-state {
  transition: opacity 0.218s;
  bottom: 0;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
}

.gsi-material-button:disabled {
  cursor: default;
  background-color: #ffffff61;
  border-color: #1f1f1f1f;
}

.gsi-material-button:disabled .gsi-material-button-contents,
.gsi-material-button:disabled .gsi-material-button-icon {
  opacity: 0.38;
}

.gsi-material-button:not(:disabled):active .gsi-material-button-state,
.gsi-material-button:not(:disabled):focus .gsi-material-button-state {
  background-color: #303030;
  opacity: 0.12;
}

.gsi-material-button:not(:disabled):hover {
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.30),
    0 1px 3px 1px rgba(60, 64, 67, 0.15);
}

.gsi-material-button:not(:disabled):hover .gsi-material-button-state {
  background-color: #303030;
  opacity: 0.08;
}

/* Animaciones para fondo y decoraciones */
@keyframes bubble1 {
  0% { transform: translateY(0) scale(1); }
  50% { transform: translateY(30px) scale(1.08); }
  100% { transform: translateY(0) scale(1); }
}
@keyframes bubble2 {
  0% { transform: translateX(0) scale(1); }
  50% { transform: translateX(-30px) scale(1.05); }
  100% { transform: translateX(0) scale(1); }
}
@keyframes bubble3 {
  0% { transform: scale(1); }
  50% { transform: scale(1.12); }
  100% { transform: scale(1); }
}
@keyframes bubble4 {
  0% { transform: translateY(0) scale(1); }
  50% { transform: translateY(-20px) scale(1.07); }
  100% { transform: translateY(0) scale(1); }
}
@keyframes bubble5 {
  0% { transform: translateX(0) scale(1); }
  50% { transform: translateX(20px) scale(1.1); }
  100% { transform: translateX(0) scale(1); }
}
.animate-bubble1 {
  animation: bubble1 14s ease-in-out infinite;
}
.animate-bubble2 {
  animation: bubble2 18s ease-in-out infinite;
}
.animate-bubble3 {
  animation: bubble3 12s ease-in-out infinite;
}
.animate-bubble4 {
  animation: bubble4 16s ease-in-out infinite;
}
.animate-bubble5 {
  animation: bubble5 20s ease-in-out infinite;
}
.animate-spin-slow {
  animation: spin 20s linear infinite;
}
@keyframes spin {
  100% { transform: rotate(360deg); }
}
.animate-pulse {
  animation: pulse 2s infinite;
}
@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.6; }
}
.animate-bounce {
  animation: bounce 1.5s infinite;
}
@keyframes bounce {
  0%, 100% { transform: translateY(0);}
  50% { transform: translateY(-8px);}
}